<template>
    <div class="box">
        <HeaderCom :tit="title" :backShow="backShow"/>
        <SwiperPic />
        <div class="con-box">
            <div class="con-nav">
                <a href="javascript:;" class="cur"></a>
                <a href="javascript:;"></a>
                <a href="javascript:;"></a>
            </div>
            <div class="con-lists">
                <ul class="con-list" style="display:block;">
                    <li class="con-item photo-pic" v-for="(item,index) in saying" :key="index">
                        <h4>{{item.title}}</h4>
                        <div class="photo-box">
                            <img v-lazy="item.imgSrc" alt="">
                            <div class="mask">
                                <p class="mov-copy">{{item.content}}</p>
                            </div>
                        </div>
                    </li>
                </ul>
                <ul class="con-list" style="display:none;">
                    <li class="con-item" v-for="(item,index) in videoList" :key="index">
                        <h4>{{item.title}}</h4>
                        <video width="100%" :poster="item.poster" :src="item.videoSrc" controls>
                            video
                        </video>
                    </li>
                </ul>
                <ul class="con-list" style="display:none;">
                    <li class="con-item photo-pic" v-for="(item,index) in photoList" :key="index">
                        <img v-lazy="item.imgSrc" alt="">
                    </li>
                </ul>
                
            </div>
        </div>
        <NavCom></NavCom>
    </div>
</template>
<script>
import NavCom from "@/components/NavCom";
import HeaderCom from "@/components/HeaderCom";
import SwiperPic from "@/components/SwiperPic"
export default {
    name: 'Home',
    data() {
        return {
            title:"",
            backShow:false,
            // 
            saying:[
                {
                    title:"",
                    imgSrc:'/static/images/bg.jpg',
                    content:','
                },
                {
                    title:"",
                    imgSrc:'/static/images/wx-dwh.jpg',
                    content:','
                },
                {
                    title:"",
                    imgSrc:'/static/images/dwh.jpg',
                    content:''
                },
                {
                    title:"",
                    imgSrc:'/static/images/ykj.jpg',
                    content:''
                },
                {
                    title:"",
                    imgSrc:'/static/images/wx.jpg',
                    content:''
                }
            ],
            // 
            videoList:[
                {
                    title:'--',
                    poster:'/static/images/bg.jpg',
                    videoSrc:'http://1252153290.vod2.myqcloud.com/da1e24bdvodgzp1252153290/df7420b35285890782905196910/SYHXJfty1qwA.mp4'
                },
                {
                    title:'--',
                    poster:'/static/images/bg.jpg',
                    videoSrc:'http://1252153290.vod2.myqcloud.com/da1e24bdvodgzp1252153290/df7420b35285890782905196910/SYHXJfty1qwA.mp4'
                },
                {
                    title:'--',
                    poster:'/static/images/bg.jpg',
                    videoSrc:'http://1252153290.vod2.myqcloud.com/da1e24bdvodgzp1252153290/df7420b35285890782905196910/SYHXJfty1qwA.mp4'
                },
                {
                    title:'--',
                    poster:'/static/images/bg.jpg',
                    videoSrc:'http://1252153290.vod2.myqcloud.com/da1e24bdvodgzp1252153290/df7420b35285890782905196910/SYHXJfty1qwA.mp4'
                },
            ],
            // 
            photoList:[
                {
                    imgSrc:'/static/images/bg.jpg'
                },
                {
                    imgSrc:'/static/images/ykj.jpg'
                },
                {
                    imgSrc:'/static/images/dwh.jpg'
                },
                {
                    imgSrc:'/static/images/wx.jpg'
                },
                {
                    imgSrc:'/static/images/wx-dwh.jpg'
                },
                {
                    imgSrc:'/static/images/st93.png'
                }
            ]
        }
    },
    mounted:function(){
        $(function(){
            $(".con-nav").on("click","a",function(){
                $(this).addClass("cur").siblings().removeClass("cur");
                $(".con-list").eq($(this).index()).show().siblings(".con-list").hide();
            })
        })
    },
    components :{
    NavCom,
    HeaderCom,
    SwiperPic
  }
}
</script>
<style scoped>
    .box{
        width: 100%;
        
    }
    .box img{
        width: 60%;
        margin: 0 auto;
    }
    .text{
        text-align: center;
        font-size: 15px;
        color: red;
    }
    .text a img{
        width: 50px;
        vertical-align: -3px;
    }
    .pic{
        width: 200px;
        height: 350px;
    }
    .con-box{
        /* background: rgba(0, 0, 0, 0.3); */
        margin: 5px;
        border-radius: 10px;
        overflow: hidden;
    }
    .con-box .con-nav{
        display: flex;
        background: #aaa;
        border-radius: 5px;
        height: 30px;
        line-height: 30px;
    }
    .con-box .con-nav a{
        display: block;
        flex: 1;
        text-align: center;
        border-left: 1px solid #ccc;
        color: #fff;
    }
    .con-box .con-nav a:nth-of-type(1){
        border-left: 0;
    }
    .con-box .con-nav a.cur{
        background: #0ea1f6;
        box-shadow: 1px 1px 10px #000;
    }
    .con-lists{
        margin-top: 5px;
    }
    .con-list{
        overflow: hidden;
        width: 100%;
    }
    .con-list .con-item{
        background: #fff;
        padding: 5px;
        margin: 5px 0;
        border-radius: 5px;
    }
    .con-list .con-item h4{
        width: 100%;
        height: 30px;
        line-height: 30px;
        font-size: 16px;
        font-weight: 300;
        text-align: center;
        color: #fff;
        background: #333;
    }
    .con-list .con-item.photo-pic img{
        display: block;
        width: 100%;
    }
    .con-list .con-item.photo-pic .photo-box{
        position: relative;
    }
    .con-list .con-item.photo-pic .mask{
        position: absolute;
        left: 0;
        top: 0;
        background: rgba(0,0,0,0.5);
        width: 100%;
        height: 100%;
    }
    .con-list .con-item.photo-pic .mov-copy{
        box-sizing: border-box;
        width: 100%;
        text-align: center;
        color: #fff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        padding: 0 50px;
        line-height: 50px;
    }
</style>



